@import url(https://fonts.googleapis.com/css2?family=Signika:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

body{
  background-color: #f5f5f7 !important;
  height: 100vh;
}
.b{
  min-height: 90vh;
  /* equal to footer height */
  margin-bottom: -12px; 
}
.b:after {
  content: "";
  display: block;
}
.f, .b:after {
  height: 12px; 
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.HeaderContainer{
    position: relative;
    display: flex;
    justify-content:center ;
    height: 5rem;
    margin-bottom: 40px;
    

}
.HeaderImage{
    /* height: 11rem; */
    /* width: 11rem; */
    height: 10rem;
    /* width: 7em; */
    padding:0 1.5em 0 1em ;

    position: relative;
    top:-20px;
    cursor: pointer;

}
@media screen  and (max-width:814px){
    .HeaderImage{
        width: auto;
        height: 8rem;
        font-size: 30rem;
    padding:0;

    }.HeaderContainer{
        
        margin-bottom: 10px;
    }
}
@media screen  and (max-width:350px){
    .HeaderImage{
        width: auto;
        height: 6.8rem;
        font-size: 30rem;
    padding:0;

    }
    .HeaderContainer{
        
        margin-bottom: 10px;
    }
}
.HomeContainer {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -9px;
}


.card {
    padding: 0 0 13px 13px !important ;
    /* height:15rem; */
    height: auto;
    border: none !important;
    background-color: #f5f5f7 !important;

  }

  .card-img{
    width: 12rem !important;
  }
.card:hover{
  cursor: pointer !important;
}

